<template>
	<div class="essay-bac">
		<div class="essay-title"><span>{{title}}</span></div>
		<div class="time"><span>{{author}}</span><span>{{time}}</span></div>
		<div class="essay-main" ref="note" v-html="markContent"></div>

	</div>

</template>

<script>
	import marked from 'marked'
	import hljs from 'highlight.js/lib/highlight';
	import javascript from 'highlight.js/lib/languages/javascript';
	import 'highlight.js/styles/atom-one-dark.css'
	import {getEssay} from '../../http/essay.js' 
	hljs.registerLanguage('javascript', javascript);

	export default {
		/* props:['content'], */
		data() {
			return {
				content: '',
				time: '',
				author: '',
				title: '',
				
			}
		},

		name: 'app',
		
		mounted() {

		},
		computed: {
			markContent() {
				return marked(this.content);
			}
		},
		created() {
			this._getEssay()
			hljs.initHighlightingOnLoad()
			var rendererMD = new marked.Renderer();
			marked.setOptions({
				renderer: rendererMD,
				gfm: true,
				tables: true,
				breaks: false,
				pedantic: false,
				sanitize: false,
				smartLists: true,
				smartypants: false,
				highlight: function(code) {
					return hljs.highlightAuto(code).value
				}
			})
		},
		methods: {
			_getEssay() {
				let params = {
					id: this.$route.query.id
				}
				getEssay(params).then((res)=>{
					this.content = res.data.essay_content
					this.time = res.data.essay_time
					this.title = res.data.essay_title
					this.author = res.data.essay_autho
				})
			}
		}

	}
</script>

<style>
	.essay-bac {
		width: 100%;
		display: flex;
		align-items: center;
		flex-direction: column;
		padding-top: 20px;
	}

	.essay-bac .essay-title {
		width: 100%;
		font-size: 34px;
		text-align: center;
		font-weight:  500;
	}
	
	.essay-bac .time {
		margin-top: 10px;
		box-shadow: 1px ;
		color: gray;
		font-size: 14px;
		width: 900px;
		color: orangered;
		padding-bottom: 5px;
		border-bottom: 1px solid orangered;
		font-style: italic;
		margin-bottom: 20px;
	}
	.essay-bac .time span{
		
		margin-right: 15px;
	
		font-family: "新宋体";
	}

	.essay-bac .essay-main {
		min-height: 500px;
		width: 900px;
	}

	.essay-bac .essay-main::-webkit-scrollbar {
		display: none;
	}

	ol,
	ul {
		box-sizing: border-box;
		padding-left: 20px;
	}




	pre {
		background: #282c34;
		display: block;
		overflow-x: auto;
		padding: 0.5em;
		color: #abb2bf;
		font-family: monospace;
		white-space: pre;
	}
</style>
