import Vue from 'vue'
import Router from 'vue-router'
import IndexPage from './views/IndexPage.vue'
import XContent from './components/x-content/XContent.vue'
import XEditor from './components/x-editor/XEditor.vue'
import XEssay from './components/x-essay/XEssay.vue'
import XPhoto from './components/x-photo/XPhoto.vue'
import XUpload from './components/x-upload/XUpload.vue'
import StudentIndex from './views/StudentIndex.vue'
import XLogin from './components/x-login/XLogin.vue'
Vue.use(Router)

export default new Router({
	routes: [{
		path: '/index',
		component: IndexPage,
		children: [{
				path: '/',
				component: XContent
			},
			{
				path: 'editor',
				component: XEditor
			},
			{
				path: 'essay',
				component: XEssay
			},
			{
				path: 'photo',
				component: XPhoto
			},
			{
				path: 'upload',
				component: XUpload
			}
		]
	}, {
		path: '/',
		component: StudentIndex,
		children: [{
			path: '/',
			component: XLogin
		},
		{
			path: '/family',
			component: XPhoto 
		}
		
		]

	}]
})

