<template>
	<div class="login-main" @mousemove="moveImg($event)">
		<div class="title">Welcome to Xz-Blog</div>
		<div class="log-box">
			<div class="log-item"><label class="tip" for="name">账 号 :</label>
				<input type="text" id="name" v-model="name" /></div>
			<div class="log-item"><label class="tip" for="password">密 码 :</label>
				<input type="password" v-model="password" id="password" /></div>
			<div class="error">{{msg}}</div>
		</div>

		<div class="button-box">
			<div class="left" @click="_register"> 注册</div>
			<div class="rigth" @click="_login">登陆</div>
		</div>

		<div class="o-login" >游客登陆</div>

		<div class="three-box">
			<div class="moon" ref="moon"><img src="../../assets/green.png" alt=""></div>
			<div class="lake" ref="lake"><img src="../../assets/lake.png" alt=""></div>
			<div class="tow1" ref="tow1"><img src="../../assets/tower.png" alt=""></div>
			<div class="tow2" ref="tow2"><img src="../../assets/tower.png" alt=""></div>
			<div class="tow3" ref="tow3"><img src="../../assets/tow3.png" alt=""></div>
			<div class="point1" ref="point1"><img src="../../assets/point3.png" alt=""></div>
			<div class="point2" ref="point2"><img src="../../assets/point2.png" alt=""></div>
		</div>

		<transition name="bounce">
			<x-register v-if="showRegister" @closeRegister="closeRegister"></x-register>
		</transition>

	</div>
</template>

<script>
	import {
		mapMutations
	} from 'vuex'
	import {
		register,
		login,
		getSession
	} from '../../http/login.js'
	import XRegister from '../../basecomponents/x-register/XRegister.vue'
	import axios from 'axios'
	export default {
		data() {
			return {
				x: 0,
				y: 0,
				name: '',
				password: '',
				showRegister: false,
				msg: ''
				
			};
		},
		created() {
			getSession().then((res) => {
			})
			this.test()
		},
		
		
		
		components: {
			XRegister
		},
		methods: {
			test() {
				axios.get('https://www.cxzweb.club/home').then( (res) => {
					console.log(res)
				})
			},
			closeRegister(query) {
				this.$bac.remove()
				this.showRegister = false
				this.name = query.userId
				this.password = query.userPassword
			},
			_register() {
				this.$bac.add()
				this.showRegister = true
			},

			_login() {
				let params = {
					userId: this.name,
					userPassword: this.password
				}
				login(params).then(res => {
					if(res.data.code === 0) {
						this.msg = '登陆成功，请稍等！' 
						const val = {
							userName: res.data.msg.userName,
							avatarURL: res.data.msg.avatarURL,
							userId: res.data.msg.userId
						} 
						this.setToken(val)
						this.$router.push('family')
					} else {
						this.msg = '请输入正确的账号密码！'
					}
				})
			},

			_getSession() {
				getSession().then((res) => {
					console.log(res)
				})
			},

			moveImg(e) {
				let x_move = e.movementX < 0 ? -1 : 1;
				let y_move = e.movementY < 0 ? -1 : 1;
				for (let i = 0; i < Object.keys(this.$refs).length; i++) {
					this.x = this.x + 0.02 * x_move;
					this.y = this.y + 0.02 * y_move;
					let val_x = this.x + 'px'
					let val_y = this.y + 'px'
					this.$refs[Object.keys(this.$refs)[i]].style.transform = `translate(${val_x},${val_y})`
				}
			},

			close() {
				this.$emit('closeLogin', false)
			},

			...mapMutations(['setToken'])
		}
	}
</script>

<style scoped>
	.login-main {
		background-color: rgb(32, 25, 50);
		background-image: url(../../assets/login-bac.png);
		height: 100%;
		background-repeat: no-repeat;
		color: white;
		position: absolute;
		width: 100%;
	}

	.title {
		color: white;
		box-sizing: border-box;
		padding-left: 300px;
		padding-top: 40px;
		background-size: contain;
		background-repeat: no-repeat;
		font-size: 30px;
		font-weight: bold;
		margin-bottom: 200px;
	}

	.log-box {
		padding-left: 280px;
		font-size: 22px;
		position: relative;
	}
	
	.log-box .error{
		color: deeppink;
		position: absolute;
		font-size: 14px;
		top: 120px;
		text-indent: 130px;
		
	} 

	.log-box:before {
		content: "";
		background: url(../../assets/left.png);
		left: 150px;
		position: absolute;
		top: -30px;
		width: 92px;
		height: 70px;
		background-repeat: no-repeat;
		background-size: contain;
	}

	.log-item {
		margin-bottom: 50px;

	}

	.log-box input:focus {
		color: rgb(7, 206, 140);
	}

	.log-box input {
		font-size: 18px;
		background: none;
		border: none;
		border-bottom: 1px solid white;
		box-sizing: border-box;
		margin-left: 10px;
		color: white;
		width: 260px;
		text-indent: 5px;
	}

	.log-box input:focus {
		outline: none;
		color: darkorange;
		border-bottom: 1px solid rgb(7, 206, 140);
	}

	.button-box {
		padding-left: 320px;
		display: flex;
	}

	.button-box div {
		width: 120px;
		height: 50px;
		font-size: 22px;
		background-image: linear-gradient(to bottom, #00D58E, rgb(0, 179, 94));
		border-radius: 8px;
		text-align: center;
		line-height: 50px;
		cursor: pointer;
		margin-right: 50px;
		user-select: none;
		letter-spacing: 2px;
	}
	
	.button-box div:active{
		background-image:none;
		background-color: hotpink;
	}

	.o-login {
		margin-left: 200px;
		margin-top: 170px;
		width: 240px;
		height: 100px;
		font-size: 26px;
		background-image: linear-gradient(to bottom, orange, darkorange);
		border-radius: 8px;
		text-align: center;
		line-height: 100px;
		cursor: pointer;
		margin-right: 50px;
		user-select: none;
		letter-spacing: 3px;
	}


	.three-box {
		position: absolute;
		width: 600px;
		height: 500px;

		left: 60%;
		top: 170px;
		transform-style: preserve-3d;
	}

	.three-box div {
		position: absolute;
		left: 0px;
		top: 0px;
	}

	.three-box .moon {
		width: 400px;
		height: 430px;
	}

	.three-box img {
		width: 100%;
		height: 100%;
	}

	.three-box .lake {
		top: 260px;
		left: 100px;
		width: 370px;
		height: 90px;
		transform: translateZ(-20px);
	}

	.three-box .tow1,
	.three-box .tow2 {
		width: 73px;
	}

	.three-box .tow1 {
		top: 80px;
		top: 100px;
		width: 100px;
	}

	.three-box .tow2 {
		top: 65px;
		left: 250px;
	}

	.three-box .tow3 {
		width: 65px;
		left: -80px;
		top: 100px;
	}

	.three-box .point1 {
		width: 80px;
		left: 320px;
	}

	.three-box .point2 {
		width: 40px;
		left: -80px;
		top: 400px;
	}

	.bounce-enter-active {
		animation: bounce-in .8s;
	}

	.bounce-leave-active {
		animation: bounce-in .8s reverse;
	}

	@keyframes bounce-in {
		0% {
			opacity: 0;
			transform: scale(0);
		}

		50% {
			transform: scale(1.5);
		}

		100% {
			opacity: 1;
			transform: scale(1);
		}
	}
</style>
