<template>
	<div class="main">
		<transition name="loading">
			<x-loading v-if="showLoading"></x-loading>
		</transition>
		<img id="background" alt="background" src="./demo/css/1.jpg" />

		<div class="photo-box">
			<div class="photo-scroll" ref="photo">

				<div class="photo-main" v-for="(item,index) in photoList">
					<div class="img-box" :class="`sty${index%10}`">
						<img :src="item" alt="">
					</div>

				</div>
			</div>
		</div>

		<!--  -->

		<div class="sendMsg-box">
			<h1 class="iconfont icon-gantanhao"></h1>
			<div class="msg">
				<div class="msg-box" ref="chat">
					<div class="info" v-for="(item, index) in msgList" :key="index">
						<div class="info-name">{{item.message.name}}:&nbsp;&nbsp;</div>
						<div class="info-msg">{{item.message.msg}}</div>
					</div>
				</div>
			</div>


			<div class="student-main">
				
				<div class="iconfont icon-left draw" @click="moveScoll(true)"></div>
				<div class="student" ref="liveBox">
					<div class="student-box">
						<div class="student-info" v-for="item in liveList" :key="item.userId">
							<img :src="item.avatarURL" alt="">
							<div class="name">{{item.userName}}</div>
						</div>
					</div>
				</div>
				<div class="iconfont icon-left draw right" @click="moveScoll(false)"></div>
			</div>

			<div class="send">
				<input type="text" placeholder="请输入内容" v-model="message" @keydown.13="sendMessage">
				<span class="iconfont icon-send" @click="sendMessage"></span>
			</div>

		</div>
		
	</div>
</template>

<script>
	import RainyDay from './demo/js/rainyday.js'
	import io from 'socket.io-client'
	import XLoading from '../../basecomponents/x-loading/XLoading.vue'
	import photo from './photo.js'
	import {
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				message: '',
				socket: '',
				msgList: [],
				liveList: [],
				my: null,
				can: true,
				showLoading: true,
				photoList: []
			}
		},
		created() {
			this.photoList = photo
			/* this.initPhoto() */
			this.initMy()

		},
		components: {
			XLoading
		},
		mounted() {
			this.run()
			this.webSocket()
			this.movePhoto()

		},
		methods: {
			movePhoto() {
				let val = this.$refs.photo.clientHeight - document.body.clientHeight
				let str = `@keyframes photo {
								to{
									transform: translateY(-${val}px);
								}
							}`
				var style = document.createElement('style');
				style.type = 'text/css';
				style.innerHTML = str;
				document.getElementsByTagName('head')[0].appendChild(style);
			},

			initPhoto() {
				photo.forEach((item) => {
					let url = require(item)
					console.log(item, url)
					photoList.push(url)
				})
			},

			moveScoll(con) {
				if (!this.can) {
					return
				}
				this.can = false

				let val = this.$refs.liveBox.scrollLeft
				if (con) {
					let index = 0

					let id = setInterval(() => {
						val = val - 8;
						index++
						this.$refs.liveBox.scrollLeft = val
						if (index >= 20) {
							clearInterval(id)
							this.can = true
						}
					}, 16)
				} else {
					let index = 0
					let id = setInterval(() => {
						val = val + 8;
						index++
						this.$refs.liveBox.scrollLeft = val
						if (index >= 20) {
							clearInterval(id)
							this.can = true
						}
					}, 16)
				}
			},
			initMy() {
				this.my = JSON.parse(localStorage.getItem('xz-name'))
			},

			sendMessage() {
				this.socket.emit('serve', {
					msg: this.message,
					name: this.my.userName
				})
				this.message = ''
			},

			webSocket() {
				const log = console.log
				this.userId = `client_${Math.random()}`
				this.socket = io('https://www.cxzweb.club/api/io', {
					// 实际使用中可以在这里传递参数
					query: {
						room: `demo`,
						user: JSON.stringify(this.my)
					},

					transports: ['websocket']
				});
				/* window.socket = this.socket */
				this.socket.on('connect', () => {
					const id = this.socket.id;

					log('#connect,', id, this.socket);
					this.socket.on('res', '我来了')
					// 监听自身 id 以实现 p2p 通讯
					this.socket.on(id, msg => {
						log('#receive,', msg);

					});


				});

				this.socket.on('online', msg => {
					log('#receive,', msg);


					this.liveList = msg.all
					/* this.peopleNum = msg.clients.length + 1 */
				});
				// 接收在线用户信息
				this.socket.on('chat', msg => {
					this.msgList.push(msg)
				});

				// 系统事件
				this.socket.on('disconnect', msg => {
					log('#disconnect', msg);
				});

				this.socket.on('disconnecting', () => {
					log('#disconnecting');
				});

				this.socket.on('error', () => {
					log('#error');
				});
			},


			run() {
				var image = document.getElementById('background');
				var that = this
				image.onload = function() {
					var engine = new RainyDay({
						image: this
					});
					engine.rain([
						[1, 2, 8000]
					]);
					engine.rain([
						[3, 3, 0.88],
						[5, 5, 0.9],
						[6, 2, 1]
					], 100);

					setTimeout(() => {
						that.showLoading = false
					}, 1000)
				};
			},

			randomImg(imgList) {
				let index = 0
				let end = imgList.length

				function randomPosition() {
					img.left = ''

				}
			},

			...mapMutations(['getToken'])
		}
	}
</script>

<style scoped>
	.main {
		box-sizing: border-box;
		width: 100%;
		height: 100%;
		overflow: hidden;
	}

	#background {

		box-sizing: border-box;
		width: 100%;
		height: 100%;
		opacity: 0;
	}

	.photo-box {
		padding: 10px;
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: 999;
		left: 0px;
		top: 0px;
		overflow: hidden;
	}

	.photo-box .photo-scroll {
		width: 100%;
		flex-shrink: 0;
		box-sizing: border-box;
		flex-direction: row;
		align-items: flex-start;
		flex-wrap: wrap;
		display: inline-flex;
		animation: photo linear 300s infinite alternate;
	}

	.photo-main {
		flex: 0 0 33.3%;
		display: flex;
		padding: 5px;
		height: 400px;
		box-sizing: border-box;
		flex-direction: row;
		justify-content: center;
		align-items: center;

	}

	.img-box {
		padding: 10px;
		border: 1px solid white;
		width: 300px;
		height: 200px;

	}

	.photo-main img {
		display: block;
		width: 300px;
		height: 200px;
	}

	.sty0 {
		transform: translateX(-30px) rotate(-30deg);
	}


	.sty1 {
		transform: rotate(0deg);
	}

	.sty2 {
		transform: translateX(30px) rotate(50deg);
	}

	.sty3 {
		transform: translateX(-50px) rotate(-30deg);
	}

	.sty4 {
		transform: translateX(10px) rotate(20deg);
	}

	.sty5 {
		transform: translateX(30px) rotate(-50deg);
	}

	.sty6 {
		transform: translateX(-30px) rotate(-20deg);
	}

	.sty7 {
		transform: rotate(-60deg);
	}

	.sty8 {
		transform: translateX(30px) rotate(-15deg);
	}

	.sty9 {
		transform: translateX(-30px) rotate(-40deg);
	}

	.sty10 {
		transform: rotate(40deg);
	}


	.sendMsg-box {
		border-radius: 10px 10px 0 0;
		background: rgba(0, 0, 0, 0.4);
		width: 90%;
		position: fixed;
		bottom: 0px;
		left: 50%;
		transform: translateX(-50%);
		z-index: 1000;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}



	.student {
		width: 90%;

		height: 60px;

		overflow-x: scroll;
	}

	.student-main {
		position: relative;
		width: 100%;
		display: flex;
		justify-content: center;
	}
	
	h1{
		position: absolute;
		right: 10px;
		top: 5px;
		tab-size: 0px;
		font-size: 20px;
		color: white;
	}


	.student .student-box {
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;

	}


	.student::-webkit-scrollbar {
		display: none;
	}

	.name {
		position: absolute;
		bottom: -2px;
		font-size: 12px;
		width: 100%;
		background-color: rgba(0, 213, 142, 0.8);
		text-align: center;
		height: 20px;
		color: black;
		line-height: 14px;

	}

	.student-info {
		flex-basis: 60px;
		flex-shrink: 0;
		width: 60px;
		height: 60px;
		position: relative;
		border-radius: 50%;
		overflow: hidden;
		margin-right: 15px;
		animation: avatar linear 0.3s;

	}

	@keyframes avatar {
		0% {
			transform: translate(-5px, -10px);
			opacity: 0;
		}

		100% {
			transform: translate(0px, 0px);
			opacity: 1;
		}
	}

	.student-info img {
		width: 100%;
		height: 100%;

	}

	.send {
		height: 60px;
		width: 97%;
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
	}

	.sendMsg-box input {
		width: 100%;
		height: 36px;
		border-radius: 20px;
		background: none;
		outline: none;
		background-color: rgba(0, 0, 0, 0.3);
		border: 1px solid #00D58E;
		color: white;
		font-size: 16px;
		padding-left: 10px;
		line-height: 36px;
	}

	.sendMsg-box span {
		background-color: #00D58E;
		cursor: pointer;
		height: 36px;
		width: 36px;
		text-align: center;
		line-height: 36px;
		border-radius: 50%;
		color: white;
		margin-left: 10px;
		user-select: none;
		box-sizing: border-box;
		font-size: 20px;
	}

	.sendMsg-box span:active {
		background-color: #00B35E;

	}

	.msg {
		width: 100%;
		height: 400px;
		box-sizing: border-box;
		padding: 20px;
		
	}

	.msg-box {
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.2);
		border-radius: 10px 10px 0 0;
		box-sizing: border-box;
		padding: 15px;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		align-content: flex-start;
		overflow-y: scroll;
		flex-wrap: wrap;
	}

	.msg-box::-webkit-scrollbar {
		display: none;
	}


	.msg-box .info {
		background-color: #00D58E;
		display: flex;
		color: white;
		padding-left: 12px;
		padding-right: 15px;
		border-radius: 15px;
		height: 24px;
		line-height: 24px;
		margin-right: 10px;
		margin-bottom: 10px;
		animation: out ease-in-out 0.4s;
	}

	.info .info-name {
		line-height: 24px;
	}

	@keyframes out {
		0% {
			opacity: 0;
			transform: translateX(-5px);
		}

		100% {
			opacity: 1;
			transform: translateX(0px);
		}
	}

	.draw {
		color: white;
		font-size: 36px;
		line-height: 60px;
		cursor: pointer;
	}

	.draw:active {
		color: hotpink;
	}

	.right {
		transform: rotateY(180deg);
	}


	.loading-enter-active {
		animation: loading-in .8s;
	}

	.loading-leave-active {
		animation: loading-in 1s reverse;
	}

	@keyframes loading-in {
		0% {
			opacity: 0.2;
			transform: translateY(-100%);
		}

		100% {
			opacity: 1;
			transform: translateY(0);
		}
	}
	
	/* @keyframes photo {
		
	
		100% {
			
			transform: translateY(-500px);
		}
	} */
</style>
