import {baseSrc} from './baseSrc.js'
import request from './http.js'
import axios from 'axios'

export const register = function(params) {
	let url = baseSrc + '/register'
	let methods = 'post'
	return request(params, url, methods)
}



export const login = function(params) {
	let url = baseSrc + '/login'
	let methods = 'post'
	return request(params, url, methods)
}


export const getSession = function() {
	let url = baseSrc + '/getSession'
	let methods = 'get'
	return request({}, url, methods)
}


