import Vue from 'vue'
import Vuex from 'vuex'

const XZ_KEY = 'xz-name'

Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		userInfo: { }
	},
	mutations: {
		setToken(state, val) {
			const val_1 = JSON.stringify(val)
			localStorage.setItem(XZ_KEY, val_1)
			state.userInfo = val
		}
	},
	getters: {
		getToken(state) {
		
		}
	}
})

export default store
