<template>
	<div class="register-main">
		<label class="photo common iconfont icon-UploadtoCloud">
			<input type="file" @change="uploadPhoto"ref="myFile">
			<img :src="uri" v-if="uri">
		</label>
		
		<div class="userName common">
			<label class="common-label" >昵称:</label><input type="text"  v-model="userName"/>
		</div>
		
		<div class="userId common">
			<label class="common-label">账号:</label><input type="text"   v-model="userId"/>
		</div>
		
		<div class="userPassword common">
			<label class="common-label">密码:</label><input type="text"   v-model="userPassword"/>
		</div>
		
		<div class="tip">{{tip}}<img v-show="showLoad" class="load" src="../../assets/loading-spinning-bubbles.svg" alt=""></div>
		
		<div class="btn">
			<div class="confirm" @click="_register">确认</div>
			<div class="cancel" @click="closeRegister">取消</div>
		</div>
	</div>
</template>

<script>
	import {uploadAvatar} from '../../http/upload.js'
	import {register} from '../../http/login.js'
	import {baseSrc} from '../../http/baseSrc.js'
	export default {
		data() {
			return {
				showLoad:false,
				uri: '',
				show: false,
				userName:'',
				userId: '',
				userPassword: '',
				avatarURL: '',
				tip: '',
				control: true
				
			};
		},
		computed:{
			url() {
				this.show = true
				return this.uri
			}
		},
		
		methods: {
			closeRegister() {
				let query = {
					userId: this.userId,
					userPassword: this.userPassword
				}
				this.$emit('closeRegister', query)
				
			},
			
			uploadPhoto() {
				const file = this.$refs.myFile.files[0]
				if(!file) {
					return
				}
				uploadAvatar(file).then((res)=>{
					this.avatarURL =   baseSrc + res.data.path
				})
				let reader = new FileReader() //声明一个FileReader实例 
				const that = this
				reader.onloadend = function(e) {
					that.uri = this.result
				}
				reader.readAsDataURL(file)
			},
			
			_register() {
				if(!this.control) {
					return
				}
				
				if(this.userName&&this.userPassword&&this.userId&&this.uri)
				{
					this.tip = ''
					this.showLoad = true
					this.control = false
					let params = {
						userName: this.userName,
						userPassword: this.userPassword,
						userId: this.userId,
						avatarURL : this.avatarURL
					}
					register(params).then( (res)=> {
						this.showLoad = false
						if(res.data.code === 0) {
							this.tip = '恭喜你，注册成功！'
							setTimeout(()=>{
								this.closeRegister()
							},20)
							
						} else  if(res.data.code === 1) {
							this.tip = '昵称或者账号已被其他小伙伴注册!'
							this.control = true
						} else {
							this.tip = '服务器内部错误，请稍后再试！'
							this.control = true
						}
					})
					
				} else {
					this.tip = '请上传头像并把注册资料填写完整哦!'
					return
				}
			}
		}
	}
</script>

<style scoped>
	.register-main {
		width: 500px;
		height: 500px;
		display: flex;
		background-color: rgb(255,255,255,1);
		position: absolute;
		left: 50%;
		top: 50%;
		margin-left: -250px;
		margin-top: -250px;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
		border-radius: 20px;
		z-index: 500;
		box-sizing: border-box;
		padding: 20px;
		color: #00D58E;
		box-shadow: 0px 1px 10px 10px #00D58E ;
	}
	
	.photo{
		background: #00D58E;
		width: 100px;
		height: 100px;
		border-radius: 50%;
		cursor: pointer;
		text-align: center;
		line-height: 100px;
		font-size: 36px;
		color: white;
		position: relative;
	}
	
	.photo img{
		width: 100px;
		height: 100px;
		border-radius: 50%;
		position: absolute;
		left: 0px;
		top: 0px;
	}
	
	.photo input {
		display: none;
	}
	
	input {
		border-bottom: 1px solid #00D58E;
		width: 300px;
		font-size: 20px;
	}
	.common{
		margin-bottom: 35px;
	}
	
	.common-label{
		font-size: 24px;
		margin-right: 20px;
	}
	
	.btn{
		display: flex;
		width: 400px;
		justify-content: space-around;
		margin-top: 20px;
		
	}
	
	.btn div{
		box-sizing: border-box;
		width: 120px;
		height: 50px;
		font-size: 22px;
		background-image: linear-gradient(to bottom, orange, darkorange);
		border-radius:8px;
		text-align: center;
		line-height: 50px;
		cursor: pointer;
		margin-top: 20px;
		user-select: none;
		letter-spacing: 3px;
		color: white;
	}
	
	.btn div:active{
		background-color: lightgray;
		background-image:none;
	}
	
	.btn .active{
		background-color: lightgray;
		background-image:none;
	}
	
	
	.tip{
		color: hotpink;
		position: absolute;
		top: 335px;
		display: flex;
		height: 50px;
		width: 100%;
		justify-content: center;
		align-items: center;
	}
	
	.load{
		width: 100px;
		height: 50px;
	}
</style>
