<template>
	<div class="x-content">
		<div class="content-item" v-for="item in list">
			<div class="editor"><img src="../../assets/listview.svg" alt=""></div>
			<div class="title">{{item.essay_title}}</div>
			<div class="dec-box">
				<div class="left">
					<img src="../../assets/test.jpg">
				</div>

				<div class="right">
					<p>{{item.essay_desc}}</p>
					<h5>
						<span class="time icon"><img src="../../assets/shijian-pink.svg" alt=""></span>
						<span>{{item.essay_editor | getTime }}</span>
						<span class="icon"><img src="../../assets/kanguo-pink.svg" alt=""></span>
						<span>{{item.essay_read}}次</span>
						<span class="icon"><img src="../../assets/xiangqu-pink.svg" alt=""></span>
						<span>{{item.essay_love}}</span>
					</h5>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
	import {
		getAllEssay
	} from '../../http/essay.js'
	import {
		getTime
	} from '../../common/util.js'
	export default {
		data() {
			return {
				list: []
			};
		},
		created() {
			this.getAll()
		},
		methods: {
			getAll() {
				getAllEssay().then((res) => {
					this.list = res.data
				})
			}
		},
		filters: {
			getTime
		}
	}
</script>

<style scoped>
	.x-content {
		padding-top: 40px;
		display: flex;
		align-items: center;
		flex-direction: column;
	}

	.content-item {
		margin-bottom: 50px;
		display: flex;
		height: 240px;
		width: 1000px;
		box-shadow: 0px 0px 10px 1px lightgray;
		flex-direction: column;
		box-sizing: border-box;
		padding: 20px;
		transition: all 0.2s ease-in;
		position: relative;
	}

	.content-item .editor {
		position: absolute;
		right: 14px;
		top: 12px;
		width: 26px;
		height: 26px;
	}

	.content-item .editor img {
		width: 100%;
		height: 100%;
		display: block;
	}

	.content-item:active {
		transform: translate(5px, 5px);
		opacity: 0.2;
	}

	.content-item .title {
		height: 50px;
		width: 100%;
		text-align: left;
		font-size: 20px;
		font-weight: bold;
	}

	.dec-box {
		display: flex;
		flex-direction: row;
	}

	.dec-box .left {
		width: 370px;
		margin-right: 20px;
	}

	.content-item .left img {
		width: 100%;
		height: 100%;
	}

	.right {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	.dec-box .right p {
		text-indent: 30px;
		font-size: 16px;
		line-height: 18px;
	}

	.dec-box .right h5 {
		text-align: right;
		height: 20px;
	}

	.dec-box .right h5 span {
		display: inline-block;
		height: 20px;
		vertical-align: text-bottom;
		margin-right: 5px;
		color: dimgray;
	}

	.dec-box .right h5 .icon img {
		width: 16px;
		height: 16px;
	}

	.x-content {
		animation: inOut 0.4s ease-in forwards;

	}

	@keyframes inOut {
		0% {
			transform: translate(-10px, -10px);
			opacity: 0;
		}

		100% {
			transform: translateX(0, 0);
			opacity: 1;
		}
	}
</style>
